.calendar-table-container {
    display: flex;
    justify-content: center;
}

.calendar-table {
    font-size: 24px;
    border: 4px solid white;
    margin: 16px;
    border-collapse: collapse;
}

.calendar-table th, 
.calendar-table td {
    border: 2px solid white;
    padding: 8px;
}